/*

*/

.c-navbar__checkbox,
.c-navbar__label {
    display: none;
}

.c-navbar,
.c-navbar__label {
    position: fixed;
    top: 0;
    left: 0;
    @include wp-offset(top, 0, true);
}

body:not(.is-scrolled) .c-navbar__label {
    top: 0 !important;
}

.c-navbar {
    z-index: map-get($zindex, header);

    width: 100%;

    backface-visibility: hidden;

    transition: all $transition--micro ease-out;
    transition-property: color, opacity;

    a {
        display: block;

        color: inherit;
        text-decoration: none;
    }

    li {
        display: block;
    }
}

.c-navbar__side {

    &:after {
        content: none !important;
    }
}

.c-navbar__middle {
    text-align: center;
}
