.c-navbar {
    position: fixed;
    font-size: 12px;

    ul ul {
        padding-right: 0;
    }

    li {
        display: inline-block;

        li {
            padding-left: 0;
        }
    }

    a {
        text-decoration: none;
    }
}

.c-navbar__content {
    display: flex;
    align-items: center;
    justify-content: center;
    height: $header-height;
    transition: top 1s $easeInOutCirc;
    border: 0 solid transparent;
    padding-top: 0 !important;
}

.c-navbar__middle {
    flex-shrink: 0;
}

.c-navbar__side--left {
    order: -1;
}

.c-navbar__side {
    flex: 1 1 50%;

    &:before {
        content: none !important;
    }
}

.c-navbar--inverse {
    color: #FFFFFF;

    .c-branding img {
        filter: invert(1);
    }
}

.has-left-menu:not(.has-right-menu) {
    .c-navbar__middle {
        text-align: right;
    }

    .c-navbar__side--left {
        margin-left: 0;
        margin-right: auto;

        text-align: left;
    }
}

.has-right-menu:not(.has-left-menu) {
    .c-navbar__middle {
        text-align: left;
    }

    .c-navbar__side--right {
        margin-right: 0;
        margin-left: auto;

        text-align: right;
    }
}

.has-left-menu.has-right-menu {
    .c-navbar__side--left {
        text-align: right;
    }

    .c-navbar__side--right {
        text-align: left;
    }

    .c-navbar__middle {
        text-align: center;
    }
}