@import "components/navbar/main";
@import "components/navbar/burger";

@include below(lap) {
    @import "components/navbar/mobile";
}

@include below(small) {
    .c-navbar__content {
        padding-top: 70px;
    }
}


@include above(lap) {
    @import "components/navbar/desktop";
    @import "components/navbar/dropdown";
}

.c-navbar {
    &:before,
    &:after {
        content: none;
    }
}

.sub-menu,
.children {
    border-width: 0;
}

.c-navbar--dropdown {
    .sub-menu,
    .children {
        @extend %ml--s;
        @extend %pt-xs;
        @extend %pb-xs;
        @extend %bt-s;
    }

    .sub-menu .sub-menu,
    .children .children {
        @extend %pt-xs;
        @extend %bl-xs;
        @extend %mt--m;
    }

    .sub-menu .menu-item,
    .children .menu-item {
        @extend %pt-xs;
        @extend %pb-xs;
        @extend %pr-s;
        @extend %pl-s;
    }

    @include below(lap) {

        ul {
            margin-left: 0 !important;
        }

        .sub-menu,
        .children {
            padding-top: 0;
            padding-bottom: 0;

            .menu-item {
                padding: 0;
            }
        }

        .sub-menu .sub-menu,
        .children .children {
            margin-top: 0;
            padding-top: 0;
        }
    }
}

