.has-hero {

    @include below('lap') {
        .c-navbar__middle {
            transform: translate3d(0, 100vh, 0);
        }
    }

    @include above('lap') {

        &:not(.is-scrolled),
        &.u-static-header{

            .c-navbar {
                position: absolute;
                transform: translate3d(0, 100vh, 0);
            }
        }
    }
}
